.popup {
  width: 100%;
  min-height: 100%;
  z-index: 1;
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
 
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s, opacity 0.5s;
  justify-content: center;
  align-items: center;
  display: flex;
 
}


