.profile {
  max-width: 880px;
  margin: 0 auto;
  padding-top: 40px;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 895px) {
  .profile {
    max-width: 552px;
    margin: 40px auto 36px auto;
    flex-direction: column;
    align-items: center;
  }
}

